.feed{
    flex:0.4;
    border-right:1px solid var(--twitter-background0);
    min-width: fit-content;
    overflow-y: scroll;
}

.feed_header{
    position: sticky;
    top:0;
    background-color: white;
    z-index: 100;
    border: 1px solid var(--twitter-background0);
    padding: 15px 20px;
}

.feed_header h2{
    font-size: 20px;
    font-weight: bold;
}



/* hides scrolebar  */
.feed::-webkit-scrollbar{
    display: none;
}








