.post{
    display: flex;
    align-items: flex-start;
    border-bottom: 1px solid var(--twitter-background);
    padding-bottom: 10px;

}

.post_body{
    flex:1;
    padding: 10px;
}

.post_body img{
    border-radius: 20px;

}

.post_footer{
    display: flex;
    justify-content: space-between;
    margin-top: 10px;

}


.post_footer .icon{
    height:20px;
}  


.post_footer .comment:hover{
    background-color: var(--twitter-background);
    border-radius: 10px;

}

.post_footer .retweet:hover{
    background-color: rgb(82, 170, 82);
    border-radius:50%;

}
.post_footer .like:hover{
    background-color:rgb(231, 108, 108);
    border-radius: 10px;

}
.post_footer .share:hover{
    background-color: var(--twitter-background);
    border-radius: 10px;

}





.post_badge{
    font-size: 14px !important;
    color: var(--twitter-color);
}

.post_headerSpecial{
    font-weight: 600;
    font-size: 12px;
    color:gray;
}


.post_header_text h3{
    font-size: 15px ;
    margin-bottom: 5px;

}

.post_headerDesc{
    margin-bottom: 10px;
    font-size: 15px;

}

.post_avatar{
    padding:20px;
}